<template>
  <div class="list">
    <ul class="order">
      <li v-for="(item, index) in orders" @click="listOrder(item.id)" :key="index">
        {{item.text}}
        <i>&#9660;</i>
      </li>
    </ul>
    <!-- 商品列表 -->
    <div class="main">
      <Product v-for="(item, index) in dealList" :key="index" :data="item"></Product>
    </div>
    <!-- 加载未显示的 -->
    <div class="loader" @click="drop" v-show="others.length">显示其余的{{others.length}}条<span class="drop"></span>
    </div>
    <p class="prompt" v-show="!others.length">已经到底了哦！</p>
  </div>
</template>

<script>
//引入组件
import Product from "../components/Product";
export default {
  //注册组件
  components: { Product },
  //数据
  data() {
    return {
      //数据排序
      orders: [
        { text: "价格排序", id: "price" },
        { text: "销量排序", id: "sales" },
        { text: "好评排序", id: "evaluate" },
        { text: "优惠排序", id: "discount" }
      ],
      // 储存数据
      list: [],
      //储存其余未显示的
      others: []
    }
  },
  computed:{
    dealList(){
      return this.list.filter(item => item.title.toUpperCase().indexOf(this.$store.state.search.toUpperCase()) >= 0);
    }
  },
  methods:{
    //展示剩余的信息
    drop(){
      //将others里面的信息存储到list里面
      this.list = this.list.concat(this.others);
      //清空others
      this.others = [];
    },
    //数据排序
    listOrder(id){
      //优惠排序
      if(id === 'discount'){
        this.list.sort((a,b) => {
          return (a.originPrice - a.price) - (b.originPrice - b.price)
        })
        return;
      }
      this.list.sort((a,b) =>{
        return b[id] - a[id]
      })
    }
  },
  //容器搭建完成
  created() {
    //发送请求
    this.$http
      //get请求
      .get('/data/list.json?id=' + this.$route.params.id)
      //监听返回的数据
      .then(({ data }) => {
        //储存已经显示的
        this.list = data.slice(0, 6);
        //储存还未显示的
        this.others = data.slice(6);
      });
  }
}
</script>

