.list {
  background-color: #fff;
  .order {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    border-bottom: 1px solid #ccc;
  }
  li {
    color: #666;
    font-size: 12px;
    text-align: center;
    line-height: 24px;
    border-right: 1px solid #ccc;
  }
  :last-child {
    border-right: none;
  }
  i {
    color: #999;
    font-size: 10px;
  }
  .main {
    width: 94%;
    margin: 10px auto 0;
  }
  .loader {
    color: #f90;
    text-align: center;
    line-height: 40px;
    .drop {
      display: inline-block;
      width: 8px;
      height: 8px;
      margin: 3px 0 3px 5px;
      border-bottom: 2px solid;
      border-left: 2px solid;
      transform: rotate(-45deg);
    }
  }
}
</style>

