<template>
  <div class="detail">
    <!-- 图片信息 -->
    <div class="img">
      <img :src="'/image/item/' + data.src" alt />
      <div class="text">
        <h1>{{data.title}}</h1>
        <p>{{data.description}}</p>
      </div>
    </div>
    <div class="main">
      <!-- 价格信息 -->
      <div class="price">
        <em>{{data.price}}元</em>
        <em>门市价:{{data.originPrice}}元</em>
        <em>立即购买</em>
      </div>
      <!-- 销量信息 -->
      <div class="sales">
        <b>支持立即退货</b>
        <b>支持随时退货</b>
        <b>销量{{data.sales}}</b>
      </div>
      <!-- 商店信息 -->
      <div class="store">
        <h4>店家信息</h4>
        <p>{{data.storeName}}</p>
        <p>{{data.storeAddress}}</p>
        <p>查看{{data.storeNum}}家分店</p>
      </div>
      <!-- 购买须知 -->
      <div class="moudle">
        <h4>购买须知</h4>
        <h3>有效日期</h3>
        <p>{{data.validateTime}}</p>
        <h3>使用日期</h3>
        <p>{{data.useTime}}</p>
        <h3>使用规则</h3>
        <p v-for="(item, index) in data.rules" :key="index">{{item}}</p>
      </div>
    </div>
    <p class="prompt">已经到底了哦！</p>
  </div>
</template>

<script>
export default {
  //绑定数据
  data() {
    return {
      data: {}
    };
  },
  methods: {
    updateData() {
      //获取路由参数
      let { params } = this.$route;
      //发送请求
      this.$http
        //get请求
        .get("/data/product.json", { params })
        //监听返回的数据
        .then(({ data }) => (this.data = data));
    }
  },
  //创建完成请求数据
  created() {
    //更新数据
    this.updateData();
  },
  //监听路由变化
  watch: {
    $route() {
      //更新数据
      this.updateData();
    }
  }
};
</script>

<style lang="scss" scoped>
.detail {
  background-color: #fff;
  .img {
    position: relative;
    .text {
      box-sizing: border-box;
      position: absolute;
      bottom: 5px;
      left: 0;
      width: 100%;
      padding: 10px;
      background-color: rgba(0, 0, 0, 0.6);
      line-height: 25px;
      h1 {
        color: #fff;
        font-size: 18px;
      }
      p {
        color: #ccc;
        font-size: 16px;
      }
    }
  }
  .main {
    width: 94%;
    margin: 0 auto;
    .price {
      padding:10px 0;
      line-height: 30px;
      border-bottom:1px solid #ccc;
      em {
        color: #666;
        font-size: 16px;
      }
      em:first-child {
        margin-right: 5px;
        color: #f90;
        font-size: 24px;
      }
      em:last-child {
        float: right;
        padding: 2px 10px;
        background-color: #f80;
        color:#fff;
        border-radius:3px;
        font-size:14px;
      }
    }
    .sales {
      clear:both;
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      margin-top: 10px;
      b {
        color: #f90;
        font-size: 14px;
        text-align: center;
      }
      :last-child{
        color:#666;
      }
    }
    .store {
      margin-top: 15px;
      h4 {
        margin-bottom: 10px;
        padding-bottom: 10px;
        color: #333;
        font-size: 18px;
        border-bottom: 1px solid #ccc;
      }
      p {
        margin-bottom: 6px;
        color: #666;
        font-size: 14px;
      }
      :last-child {
        float: right;
        color: #f90;
      }
    }
    .moudle {
      clear: both;
      padding-bottom:15px;
      h4 {
        margin-bottom: 10px;
        padding-bottom: 10px;
        color: #333;
        font-size: 18px;
        border-bottom:1px solid #ccc;
      }
      h3 {
        color: #fa0;
        font-size: 16px;
        line-height: 30px;
      }
      p {
        color: #666;
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
</style>