<template>
  <div class="header">
    <div class="top">
      <span class="back" @click="back"></span>
      <h1>在线商城--陈志强</h1>
      <span class="login">登录</span>
    </div>
    <div class="search" v-show="$route.fullPath.indexOf('/detail/') !== 0">
      <input type="text" v-model="message" @keyup.enter="showSearch" placeholder="请输入搜索关键词..." />
    </div>
  </div>
</template>

<script>
export default {
  //绑定数据
  data() {
    return {
      message: ""
    };
  },
  //搜索方法
  methods: {
    //展示搜索结果
    showSearch() {
      //发布消息
      this.$store.commit("updataSearch", this.message);
      //清空数据
      this.message = "";
    },
    //返回上一页
    back() {
      this.$router.go(-1);
    }
  }
};
</script>

<style lang="scss" scoped>
.top {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  width: 86%;
  padding: 0 7%;
  line-height: 40px;
  background-color: #f90;
  color: #f6f6f6;
  .back {
    width: 8px;
    height: 8px;
    margin-top: 15px;
    border-top: 2px solid;
    border-left: 2px solid;
    transform: rotate(-45deg);
  }
  h1 {
    font-size: 18px;
    text-align: center;
  }
  .login {
    text-align: right;
    font-size: 14px;
  }
}
.search {
  padding: 10px 0;
  background-color: #fff;
  text-align: center;
  input {
    width: 90%;
    padding: 8px 18px;
    background-color: #e8e8e8;
    color: #5a5a5a;
    border-radius: 20px;
  }
}
</style>